@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900");html,body{height:100%;padding:0;margin:0;background-color:#ebeff1;font-family:'Roboto', sans-serif}*{box-sizing:border-box}a{text-decoration:none;color:#0288D1}.header-wrapper{width:100%;height:60px;background-color:#FFF;padding-left:1em;padding-right:1em}.header-wrapper .logo{float:left;width:30%;height:100%;line-height:60px}.header-wrapper .logo img{height:15px;width:auto}.header-wrapper .warning{float:left;width:70%;text-align:right;line-height:60px}.header-wrapper .warning .alpha{background-color:#41b782;border-radius:3px;color:#FFF;padding:0.3em}.search-bar-wrapper{background-color:#2856b6;color:#FFF;padding-top:30px;padding-bottom:30px;margin-bottom:60px}.search-bar-wrapper .tip{text-align:center}.search-bar-wrapper .form{width:80%;margin:0 auto;position:relative;margin-top:20px;padding-bottom:30px;text-align:center}.search-bar-wrapper .form .e-input,.search-bar-wrapper .form .datetime-picker input{border:0;border-radius:3px;height:36px;font-family:'Roboto', sans-serif;font-size:1.1rem;padding:0.5rem;width:30%;margin-right:10px}.search-bar-wrapper .form .e-input:last-child,.search-bar-wrapper .form .datetime-picker input:last-child{margin-right:0}.search-bar-wrapper .form .e-input:focus,.search-bar-wrapper .form .datetime-picker input:focus{outline:0}.search-bar-wrapper .form .e-input.date,.search-bar-wrapper .form .datetime-picker input.date{width:20%}.search-bar-wrapper .form .datetime-picker{margin-right:10px}.search-bar-wrapper .form .datetime-picker input{width:100%;margin-right:0}.search-bar-wrapper .start-search{position:absolute;height:40px;line-height:40px;text-align:center;cursor:pointer;bottom:-50px;width:100px;background-color:#FFF;color:#2856b6;left:50%;margin-left:-50px;border-radius:3px;box-shadow:0 3px 6px rgba(0,0,0,0.01),0 3px 6px rgba(0,0,0,0.16)}.no-result,.searching{width:80%;margin:0 auto;padding-top:60px}.no-result p,.searching p{font-size:1.3rem;font-weight:300;text-align:center}.result-view{width:85%;margin:0 auto}.result-view .list{width:40%;float:left}.result-view .list h4{font-size:1.1rem;color:#6c7c83}.result-view .list h4 small{font-weight:300}.result-view .list h4 i{vertical-align:middle}.result-view .list ul{list-style:none;padding:0;margin:0}.result-view .list ul li{color:#424242;background-color:#FFF;border-radius:3px;margin-bottom:15px;padding:0.8em;box-shadow:0 2px 6px 2px rgba(0,0,0,0.05);cursor:pointer;-webkit-transition:all 0.5s ease-in-out;transition:all 0.5s ease-in-out}.result-view .list ul li:hover{box-shadow:0 2px 6px 2px rgba(0,0,0,0.1)}.result-view .list ul li i{vertical-align:middle;font-size:10px}.result-view .list ul li .requestCount{color:#2E7D32}.result-view .list ul li .responseCount{color:#F57F17}.result-view .inspect{width:55%;margin-left:5%;float:left;padding:1em}.prettyprint{box-shadow:0 2px 6px 2px rgba(0,0,0,0.05);border:0;border-radius:3px;background-color:#FFF;margin-bottom:20px;padding:1em}.code-header{color:#6C7C83;font-weight:500;margin-bottom:15px}.error{background-color:#e53935;color:#FFF;text-align:center;padding-top:15px;padding-bottom:15px}.error i{vertical-align:middle}



